import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import {FC} from "react";
import {Link} from "gatsby";
import {Container} from "@mui/material";

import { createTheme, ThemeProvider, styled } from '@mui/material/styles';
import { green, purple, blue } from '@mui/material/colors';
import CssBaseline from "@mui/material/CssBaseline";

// @ts-ignore
import ResumeFile from "../images/resume.pdf";


let theme = createTheme({
  palette: {
    primary: {
        main: "#73877B",
        light: "#839788",
        dark: "#73877B",
        contrastText: "#000000"
    },
    secondary: purple,
    background: {
        default: "#F5E4D7",
        paper: "#73877B",
      },
  },
  typography: {
    fontFamily: 'Inconsolata',
    h3:{
        color: "#73877B",
    },
    h5:{
        color: "#73877B",
    },
  }
});

const modalStyle = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    bgcolor: 'background.paper',
    width: "90%",
    height: "90%",
}

interface Props {
    window?: () => Window;
}

export const MainLayout: FC<Props> = ({
    children
}) => {

    const downloadResume = () => {
        const link = document.createElement("a");
        link.download = 'dragon_prevost_resume.pdf';
        link.href = ResumeFile;
        link.click();
    }

    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Box sx={{ display: 'flex' }}>
            <AppBar component="nav" elevation={0} sx={{backgroundColor: theme.palette.background.default }}>
                <Toolbar>
                    <Typography
                        variant="h5"
                        component="div"
                        sx={{ flexGrow: 1, display: { sm: 'block' }, fontFamily: 'Inconsolata' }}
                    >
                        <Link to={'/'} style={{textDecoration: 'none'}}>
                            <Typography variant='h3'>Dragon Prevost</Typography>
                        </Link>
                    </Typography>
                    <Button
                        onClick={downloadResume}
                        variant="outlined"
                        sx={{border: 2}}
                    >
                        <Typography variant='h5'>Download Resume</Typography>
                    </Button>
                </Toolbar>
            </AppBar>
            <Container>
                <Toolbar />
                {children}
            </Container>
        </Box>
      </ThemeProvider>
    );
}
